<template>
  <div>
    <el-dialog title="" width="4rem" :visible.sync="dialogFormVisible" @close="cancelEvent">
      <el-form :model="form">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.musicListId" placeholder="">
            <el-option
              v-for="item in this.$store.state.userMusicList"
              :key="item.musicListId"
              :label="item.musicListName"
              :value="item.musicListId"
            >
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEvent"> </el-button>
        <el-button type="primary" @click="confirmEvent"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios"
import api from "../assets/config/api"
export default {
    props:["musicId"],
    data() {
        return {
            dialogFormVisible: true,
            form: {
                musicListId: "",
                musicListName: ""
            },
            formLabelWidth: '1rem'
        }
    },
    methods:{
        cancelEvent(){
            this.dialogFormVisible = false;
            this.$emit("toggleShow");
        },
        async confirmEvent(){
            this.dialogFormVisible = false;
            this.$emit("toggleShow");
            console.log(this.musicId)
            console.log(this.form.musicListId)
            await axios.post(api.AddMusicToList,{
                musicId: this.musicId,
                musicListId: this.form.musicListId
            })
            .then(res=>{
                console.log(res)
                if(res.data.code === 400){
                  alert("")
                }
            })
            .catch(err=>{
                console.log(err)
            })
        }
    }
};
</script>