        <el-upload
          class="upload-demo"
          ref="upload"
          action= "http://47.95.229.102:8081/music/upload"
          :on-success="handleSuccess"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :file-list="musicFile"
          :auto-upload="false">
          <el-button slot="trigger" size="small" type="primary"></el-button>
          <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload"></el-button>
          <div slot="tip" class="el-upload__tip"></div>
      </el-upload>-->

      <!-- <div slot="footer" class="dialog-footer">
          <el-button @click="uploadAlert = false"> </el-button>
          <el-button type="primary" @click="dialogFormVisible = false"> </el-button>
      </div>-->
      <!-- </el-dialog> -->

