      <div class="noLogin" v-if="!this.$store.getters.getIsLogin">
        <span class="noLogin-login" @click="toLogin"></span> |
        <span class="noLogin-register" @click="toRegister"></span>
      </div>
    </div>

    <!--  -->
    <div class="state" v-show="stateCode">
